
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
}
*{
  margin: 0px;
}

.warp{
  width: 100%;
  border-top: 1px solid #eee;
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  font-size: 0;
}
.warp img{
  width: 20px;
  height: 20px;
}
.tabberWarp img{
  margin-top: 10px;
  margin-bottom: 0px;
}
.tabberWarp{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding-bottom: 5px;
  background: #fff;
}

.itemWarp{
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.itemWarp span{
  font-size: 12px;
}

.application {
  margin-bottom: 60px;
}
.application .weui-grid__icon {
  width: 35px !important;
  height: 35px !important;
  margin: -5px auto;
}
.application .home-head {
  z-index: 2;
}
.application .home-head a {
  height: 100px;
  font-size: 13px;
  color: #000000;
}
.application .home-head .weui-grids {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  margin-top: -10px;
}
.application .home-head .weui-grids .weui-grid {
  background: #fff;
}
.application .home-head .weui-grids .weui-grid:first-child {
  border-top-left-radius: 15px;
}
.application .home-head .weui-grids .weui-grid:nth-child(4) {
  border-top-right-radius: 15px;
}
.application .home-head .weui-grids:before {
  border-top: none;
  border-right: 1px solid #F1F6F9 !important;
}
.application .home-head .weui-grids:after {
  border-left: none;
  border-bottom: 1px solid #F1F6F9 !important;
}
.application .home-head .weui-grid:nth-child(4).weui-grid:before {
  border-right: none;
}
.application .home-head .weui-grid:nth-child(4) .weui-grid__icon {
  width: 100px;
  height: 100px;
}
.application .home-head .description {
  margin: -5px auto;
}
.application .home-head .weui-grids:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: none;
  color: #D9D9D9;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.application .home-content a {
  height: 100px;
  font-size: 13px;
  color: #000000;
}
.application .home-content .log-name {
  text-align: left;
  padding: 10px;
  font-weight: bold;
  background-color: white;
  margin-top: 10px;
}
.application .home-content .log-name .log-style {
  width: 5px;
  height: 14px;
  background-color: #0094F6;
  float: left;
  margin-right: 5px;
  margin-top: 2px;
}
.application .home-content .description {
  margin: -5px auto;
}
.application .home-content .weui-grids:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #F1F6F9 !important;
  color: #D9D9D9;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.application .weui-grid:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #F1F6F9 !important;
  color: #D9D9D9;
  -webkit-transform-origin: 0 100%;
  transform-origin: 0 100%;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.application .weui-grid:before {
  content: " ";
  position: absolute;
  right: 0;
  top: 0;
  width: 1px;
  bottom: 0;
  border-right: 1px solid #F1F6F9 !important;
  color: #D9D9D9;
  -webkit-transform-origin: 100% 0;
  transform-origin: 100% 0;
  -webkit-transform: scaleX(0.5);
  transform: scaleX(0.5);
}
.application .weui-grids {
  background: #fff;
}
.application .weui-grids .weui-grid {
  text-decoration: none;
}
.application .weui-grids .weui-grid .vux-badge {
  position: absolute;
  top: 15px;
  right: 29px;
}
.application .weui-cells__title {
  margin-top: 0.77em;
  margin-bottom: 0.77em;
  padding-left: 15px;
  padding-right: 15px;
  color: #999999;
  font-size: 14px;
}
